<template>
<div>
  <section class="registration">

    <div class="registration__container">

      <h2 class="registration__header">Create an account</h2>

      <form @submit.prevent="" class="formreg">

        <div class="formreg__group">
          <input type="text" class="formreg__input" placeholder="first name" id="first" required v-model="firstName">
          <label for="first" class="formreg__label">First name</label>
        </div>

        <div class="formreg__group">
          <input type="text" class="formreg__input" placeholder="last name" id="last" required v-model="lastName">
          <label for="last" class="formreg__label">Last name</label>
        </div>

        <div class="formreg__group">
          <input type="text" class="formreg__input" placeholder="username" id="user" required v-model="userName">
          <label for="user" class="formreg__label">Username</label>
        </div>

        <div class="formreg__group">
          <input type="email" class="formreg__input" placeholder="email" id="email" required v-model="email">
          <label for="email" class="formreg__label">E-mail address</label>
        </div>

        <div class="formreg__group">
          <input type="password" class="formreg__input" placeholder="password" id="password" required v-model="password">
          <label for="password" class="formreg__label">Password</label>
        </div>

        <div class="formreg__group">
          <input type="password" class="formreg__input" placeholder=" repeat password" id="passrep" required>
          <label for="passrep" class="formreg__label">Repeat password</label>
        </div>

      </form>
      <h4 v-for="error in errorMessages" :key="error" style="color: crimson"> {{error}} </h4>

      <button class="registration__btn" @click="registerUser">Register</button>

    </div>
  </section>
</div>
</template>

<script>
import axios from '@/axios-auth'
export default {
  data () {
    return {
      firstName: null,
      lastName: null,
      userName: null,
      password: null,
      email: null,
      errorMessages: []
    }
  },
  methods: {
    registerUser () {
      this.errorMessages = []
      console.log('data being sent ' + this.firstName)
      axios.post('api/account/register', {
        first_name: this.firstName,
        last_name: this.lastName,
        username: this.userName,
        password: this.password,
        email: this.email
      })
        .then(res => {
          const data = res.data
          if (data.response !== 'successfully registered a new user') {
            if (typeof data.first_name !== 'undefined') {
              this.errorMessages.push('First Name: ' + data.first_name[0])
            }
            if (typeof data.last_name !== 'undefined') {
              this.errorMessages.push('Last Name: ' + data.last_name[0])
            }
            if (typeof data.username !== 'undefined') {
              this.errorMessages.push('Username: ' + data.username[0])
            }
            if (typeof data.password !== 'undefined') {
              this.errorMessages.push('password: ' + data.password[0])
            }
            if (typeof data.email !== 'undefined') {
              this.errorMessages.push('email: ' + data.email[0])
            }
          } else {
            this.$store.dispatch('login', { username: this.userName, password: this.password })
          }
        })
        .catch(err => console.log('error registration attempt', err))
    }
  }
}
</script>

