<script>
import axios from '@/axios-auth'
// import Chart from '@/components/Chart'
export default {
  // components: {
  //   appChart: Chart
  // }
  data () {
    return {
      firstName: '',
      data_is_loaded: false,
      profile: {
        total_questions_correct: null,
        total_questions_taken: null,
        total_trauma_questions_correct: null,
        total_trauma_questions_attempted: null,
        total_pediatric_questions_correct: null,
        total_pediatric_questions_attempted: null,
        total_appendecies_questions_correct: null,
        total_appendecies_questions_attempted: null,
        exams_completed: null,
        weakest_subject_display: '',
        strongest_subject_display: ''
      }
    }
  },
  created () {
    // axios.get('api/sessions/', {
    console.log('created')
    axios.get('api/account/user-page/', {
      headers: { Authorization: 'Token ' + this.$store.state.auth_token }
    })
      .then(res => {
        console.log(res)
        const data = res.data
        this.firstName = data.first_name
        this.profile.total_questions_taken = data.total_overall_questions_attempted
        this.profile.total_questions_correct = data.total_overall_questions_correct
        this.profile.total_trauma_questions_correct = data.total_trauma_questions_correct
        this.profile.total_trauma_questions_attempted = data.total_trauma_questions_attempted
        this.profile.total_pediatric_questions_correct = data.total_pediatric_questions_correct
        this.profile.total_pediatric_questions_attempted = data.total_pediatric_questions_attempted
        this.profile.total_appendecies_questions_correct = data.total_appendecies_questions_correct
        this.profile.total_appendecies_questions_attempted = data.total_appendecies_questions_attempted
        this.profile.exams_completed = data.exams_completed
        this.profile.weakest_subject_display = data.weakest_subject
        this.profile.strongest_subject_display = data.strongest_subject
        this.data_is_loaded = true
      })
      .catch(err => console.log(err))
  },
  methods: {

  }
}
</script>

