<template>
<div>
  <main class="exam__main" id="color">

    <section class="questions">

      <div class="questions__title">
        <h1 class="questions__title-text">NYC REMAC Practice Exam</h1>
        <div class="questions__question-number">
          <h2 class="questions__question-number-text">{{displayNumber}}/{{questions.length}}</h2>
        </div>
      </div>

      <div class="questions__question">
        <img src="" alt="" class="questions__question-text--image">
        <h3 class="questions__question-text">{{questionText}}</h3>
      </div>

    </section>
    <!------------------------------------------------------------------------------------------------->
    <section class="answers" v-for="(question, index) in questions.slice(questionNumber,displayNumber)" :key="index">

      <div class="answers__box">
            <div class="answers__form">
              <div v-on:click="activateRow(true, false, false, false); saveChosenAnswer(question.choice_one)"
                   ref="row"
                   :class="{'answers__form-toggle' : checked}"
                   class="answers__form-row"
                    id="row">

                <p ref="textWidth1" class="answers__text answers__label-text-1">{{ question.choice_one }}</p>

              </div>

              <div v-on:click="activateRow(false,true,false, false); saveChosenAnswer(question.choice_two)"
                   ref="rowWidth1"
                   :class="{'answers__form-toggle' : checked2}"
                   class="answers__form-row row-check-2">

                <p ref="textWidth2" class="answers__text answers__label-text-2">{{question.choice_two}}</p>

              </div>
              <div v-on:click="activateRow(false,false,true,false); ; saveChosenAnswer(question.choice_three)"
                   ref="rowWidth1"
                   :class="{'answers__form-toggle' : checked3}"
                   class="answers__form-row row-check-3">

                <p ref="textWidth3" class="answers__text answers__label-text-3">{{ question.choice_three }}</p>

              </div>

              <div v-on:click="activateRow(false,false,false,true); saveChosenAnswer(question.choice_four)"
                   ref="rowWidth1"
                   :class="{'answers__form-toggle' : checked4}"
                   class="answers__form-row row-check-4">

                <p ref="textWidth4" class="answers__text answers__label-text-4">{{ question.choice_four}}</p>

              </div>
            </div>

          <div class="answers__buttons">
            <div class="answers__buttons-previous">
              <button v-if="questionNumber > 0" class="answers__btn" @click="previousButtonPressed">Back</button>
            </div>
            <div class="answers__buttons-nex">
              <button class="answers__btn" @click="nextButtonPressed">{{ nextButtonTitle }}</button>
            </div>
          </div>
      </div>

    </section>

  </main>
</div>
</template>

<script>
import axios from '@/axios-auth'
// eslint-disable-next-line no-unused-vars
import router from '@/router'
export default {
  data () {
    return {
      checked: false,
      checked2: false,
      checked3: false,
      checked4: false,
      rowWidth: 0,
      textWidth: 0,
      percent: 0,
      // checked: false,
      // checked2: false,
      // checked3: false,
      // checked4: false,
      // // -------------------------
      // widthText1: 0,
      // widthText2: 0,
      // widthText3: 0,
      // widthText4: 0,
      // widthRow1: 0,
      // widthRow2: 0,
      // widthRow3: 0,
      // widthRow4: 0,
      // widthPercentage1: 0,
      // widthPercentage2: 0,
      // widthPercentage3: 0,
      // widthPercentage4: 0,
      // -------------------------
      sessionID: null,
      examID: null,
      questionNumber: 0,
      moreQuestionsAvailableNext: true,
      nextButtonTitle: 'Next',
      previousButtonTitle: 'Previous',
      questionAnswer: {
        questionText: null,
        questionId: null,
        answerId: null,
        userAnswerChoice: null
      },
      // Exam Variables

      questionText: 'text',
      displayNumber: 1,
      userAnswer: '',
      userAnswerArray: [],
      questions: [],
      answers: []
    }
  },
  mounted () {
    this.getQuestionsFromDatabase()
  },
  methods: {
    activateRow (val1, val2, val3, val4) {
      this.checked = val1
      this.checked2 = val2
      this.checked3 = val3
      this.checked4 = val4
    },
    // text align upon width
    // widthTextValue () {
    //   this.widthText1 = this.$refs.textWidth1.clientWidth
    //   this.widthText2 = this.$refs.textWidth2.clientWidth
    //   this.widthText3 = this.$refs.textWidth3.clientWidth
    //   this.widthText4 = this.$refs.textWidth4.clientWidth
    // },
    // rowWidthValue () {
    //   this.widthRow1 = this.$refs.rowWidth1.clientWidth
    //   this.widthRow2 = this.$refs.rowWidth2.clientWidth
    //   this.widthRow3 = this.$refs.rowWidth3.clientWidth
    //   this.widthRow4 = this.$refs.rowWidth4.clientWidth
    //   this.widthPercentage1 = this.widthRow1 * 0.97
    //   this.widthPercentage2 = this.widthRow1 * 0.97
    //   this.widthPercentage3 = this.widthRow2 * 0.97
    //   this.widthPercentage4 = this.widthRow3 * 0.97
    //   // --------------------------------------------------
    //   if (this.widthText1 === this.widthPercentage1) {
    //     this.$refs.rowWidth1.style.textAlign = 'initial'
    //   } else if (this.widthText2 === this.widthPercentage2) {
    //     this.$refs.rowWidth2.style.textAlign = 'initial'
    //   } else if (this.widthText3 === this.widthPercentage3) {
    //     this.$refs.rowWidth3.style.textAlign = 'initial'
    //   } else if (this.widthText4 === this.widthPercentage4) {
    //     this.$refs.rowWidth4.style.textAlign = 'initial'
    //   } else {
    //     this.$refs.rowWidth1.style.textAlign = 'center'
    //     this.$refs.rowWidth2.style.textAlign = 'center'
    //     this.$refs.rowWidth3.style.textAlign = 'center'
    //     this.$refs.rowWidth4.style.textAlign = 'center'
    //   }
    // },
    // ---------------------------------------------------------
    // class toggle answers
    // check () {
    //   document.getElementById('choice1').checked = true
    //   this.checked = true
    //   this.checked2 = false
    //   this.checked3 = false
    //   this.checked4 = false
    // },
    // checkTwo () {
    //   document.getElementById('choice2').checked = true
    //   this.checked2 = true
    //   this.checked = false
    //   this.checked3 = false
    //   this.checked4 = false
    // },
    // checkThree () {
    //   document.getElementById('choice3').checked = true
    //   this.checked3 = true
    //   this.checked = false
    //   this.checked2 = false
    //   this.checked4 = false
    // },
    // checkFour () {
    //   document.getElementById('choice4').checked = true
    //   this.checked4 = true
    //   this.checked = false
    //   this.checked2 = false
    //   this.checked3 = false
    // },
    resetChecks () {
      this.checked = false
      this.checked2 = false
      this.checked3 = false
      this.checked4 = false
    },
    // Exam Functions
    saveChosenAnswer (userChoice) {
      console.log('update user choice called')
      this.userAnswer = userChoice
      this.saveUserAnswerToDatabase(this.userAnswer, this.examID, this.sessionID, this.questionNumber, this.questions[this.questionNumber].question_id)
    },
    nextButtonPressed () {
      if (this.moreQuestionsAvailableNext === false) {
        // routing is done in the dispatch function
        this.$store.dispatch('setExamToCompleted', this.sessionID)
      } else {
        // this.saveUserAnswerToDatabase(this.userAnswer, this.examID, this.sessionID, this.questionNumber, this.questions[this.questionNumber].question_id)

        this.userAnswerArray[this.questionNumber] = this.userAnswer
        this.questionNumber++
        this.displayNumber++
        this.questionText = this.questions[this.questionNumber].question_text
        this.checkIfAnswerExistsAndSaveToVariable(this.examID, this.sessionID, this.questions[this.questionNumber].question_id)
        if (this.displayNumber === this.questions.length) {
          this.nextButtonTitle = 'Score Test'
          console.log('display number', this.displayNumber)
          console.log('array length', this.questions.length)
          console.log('button title', this.nextButtonTitle)

          this.moreQuestionsAvailableNext = false
        }
        this.resetChecks()
        this.checkIfLastQuestion()
      }
    },
    previousButtonPressed () {
      if (this.questionNumber >= 0) {
        this.userAnswerArray[this.questionNumber] = this.userAnswer
        this.questionNumber--
        this.displayNumber--
        this.questionText = this.questions[this.questionNumber].question_text
      } else {
        this.userAnswerArray[this.questionNumber] = this.userAnswer
        this.questionText = this.questions[this.questionNumber].question_text
        // TODO: display value goes below zero - add if loop to catch
        this.displayNumber--
      }
      this.checkIfAnswerExistsAndSaveToVariable(this.examID, this.sessionID, this.questions[this.questionNumber].question_id)
    },
    checkIfLastQuestion () {
      if (this.moreQuestionsAvailableNext === true) {
        this.nextButtonTitle = 'Next'
      } else {
        this.moreQuestionsAvailableNext = false
        this.nextButtonTitle = 'Score Test'
      }
    },
    // Network methods
    getQuestionsFromDatabase () {
      axios.get('api/exam/create_new_exam/', {
        headers: { Authorization: 'Token ' + this.$store.state.auth_token }
      })
        .then(res => {
          console.log(res)
          const data = res.data
          const examQuestions = data.session_exam.questions
          this.examID = data.session_exam.id
          this.sessionID = data.id
          this.questionText = examQuestions[0].question_text
          for (const question in examQuestions) {
            this.questions.push({
              question_text: examQuestions[question].question_text,
              choice_one: examQuestions[question].choice_one,
              choice_two: examQuestions[question].choice_two,
              choice_three: examQuestions[question].choice_three,
              choice_four: examQuestions[question].choice_four,
              correct_choice: examQuestions[question].correct_choice,
              slug: examQuestions[question].slug,
              user_choice: '',
              question_id: examQuestions[question].id
            })
            axios.get('api/exam/user-answers/', {
              headers: { Authorization: 'Token ' + this.$store.state.auth_token },
              params: { session_id: data.id }
            })
              .then(answerRes => {
                console.log(answerRes)
              })
          }
        })
        .catch(err => { console.log(err) })

      console.log('questions loaded', this.questions)
    },
    saveUserAnswerToDatabase (userAnswer, examID, sessionID, counter, questionID) {
      axios.post('api/exam/save-answer/',
        {
          current_exam: examID,
          incoming_choice: userAnswer,
          counter: counter,
          session_id: sessionID,
          question_id: questionID
        }, {
          headers: { Authorization: 'Token ' + this.$store.state.auth_token }
        }
      )
        .then(res => {
          console.log('data sent', res)
        })
        .catch(err => { console.log(err) })
    },
    checkIfAnswerExistsAndSaveToVariable (examID, sessionID, questionID) {
      // check the question to see if it was already answered and return the question information and previous choice.
      // returns nothing if no answer was previously selected
      axios.post('api/exam/check-answer/',
        {
          current_exam: examID,
          session_id: sessionID,
          question_id: questionID
        }, {
          headers: { Authorization: 'Token ' + this.$store.state.auth_token }
        }
      )
        .then(res => {
          console.log('data returned', res)
          this.questionAnswer.answerId = res.data.id
          this.questionAnswer.questionId = res.data.question
          this.questionAnswer.questionText = res.data.question_text
          this.questionAnswer.userAnswerChoice = res.data.choice_text
          this.userAnswer = res.data.choice_text
          this.displayQuestionChoiceLabel(this.userAnswer)
          console.log('user choice returned', res.data.choice_text)
        })
        .catch(err => { console.log(err) })
    },
    displayQuestionChoiceLabel (choice) {
      const question = this.questions[this.questionNumber]
      switch (choice) {
        case question.choice_one:
          this.activateRow(true, false, false, false)
          break
        case question.choice_two:
          this.activateRow(false, true, false, false)
          break
        case question.choice_three:
          this.activateRow(false, false, true, false)
          break
        case question.choice_four:
          this.activateRow(false, false, false, true)
          break
      }
    }
  }
}
</script>

