<template>
<div>
  <main v-if="isLoaded" class="exam__main" id="color">

    <section  class="questions">

      <div class="questions__title">
        <h1 class="questions__title-text questions__title-text-wrong">Exam Review</h1>
        <div class="questions__question-number">
          <h2 class="questions__question-number-text questions__question-number-text-wrong">{{reviewIndex + 1}}/{{ answersForReview.length }}</h2>
        </div>
      </div>

      <div class="questions__question">
        <img src="../../assets/ekg2.jpg" alt="" class="questions__question-text--image">
        <h3 class="questions__question-text">{{answersForReview[reviewIndex].questionText}}</h3>

      </div>

    </section>
    <!------------------------------------------------------------------------------------------------->
    <section class="answers">

      <div class="answers__box">

        <div class="answers__form">

          <div ref="row1" class="answers__form-row row-check answers__form-row-wrong">
              <p ref="text1" class="answers__text answers__label-text-1">{{answersForReview[reviewIndex].userAnswerChoice}}</p>
          </div>

          <div ref="row2" class="answers__form-row row-check-2 answers__form-row-right">
              <p ref="text2" class="answers__text answers__label-text-2">{{answersForReview[reviewIndex].correctAnswerChoice}}</p>
          </div>

          <div ref="row3" class="answers__form-row row-check-3 answers__form-row-explain">
              <p ref="text3" class="answers__text answers__label-text-3">{{answersForReview[reviewIndex].answerExplanation}}</p>
          </div>

          <div class="answers__buttons">
            <div class="answers__buttons-previous">
              <button @click="safeDecreaseIncrementIndex" class="answers__btn">Back</button>
            </div>
            <div class="answers__buttons-nex">
              <button @click="safeIncrementIndex" class="answers__btn">Next</button>
            </div>
          </div>

        </div>

      </div>

    </section>
  </main>
</div>
</template>

<script>
import axios from '@/axios-auth'

export default {
  data () {
    return {
      // row1: 0,
      // row2: 0,
      // row3: 0,
      // text1: 0,
      // text2: 0,
      // text3: 0,
      // percent1: 0,
      // percent2: 0,
      // percent3: 0
      answersForReview: [],
      reviewIndex: 0,
      isLoaded: false
      // userAnswerChoice: null,
      // correctAnswerChoice: null,
      // questionText: null,
      // answerExplanation: null
    }
  },
  beforeCreate () {
  },
  mounted () {
    this.getQuestionsForReview()
    // this.textWidth()
    // this.rowWidth()
  },
  methods: {
    textWidth () {
      this.text1 = this.$refs.text1.clientWidth
      this.text2 = this.$refs.text1.clientWidth
      this.text3 = this.$refs.text1.clientWidth
    },
    rowWidth () {
      this.row1 = this.$refs.row1.clientWidth
      this.row2 = this.$refs.row1.clientWidth
      this.row3 = this.$refs.row1.clientWidth
      this.percent1 = this.row1 * 0.97
      this.percent2 = this.row1 * 0.97
      this.percent3 = this.row1 * 0.97
      if (this.text1 === this.percent1) {
        this.$refs.row1.style.textAlign = 'initial'
      } else if (this.text2 === this.percent2) {
        this.$refs.row2.style.textAlign = 'initial'
      } else if (this.text3 === this.percent3) {
        this.$refs.rrow3.style.textAlign = 'initial'
      } else {
        this.$refs.row1.style.textAlign = 'center'
        this.$refs.row2.style.textAlign = 'center'
        this.$refs.row3.style.textAlign = 'center'
      }
    },
    getQuestionsForReview () {
      axios.post('api/exam/review/', {
        session_id: this.$store.state.examSessionId.examSessionId
      }, {
        headers: { Authorization: 'Token ' + this.$store.state.auth_token }
      })
        .then(res => {
          const answers = res.data
          for (const index in answers) {
            this.answersForReview.push({
              userAnswerChoice: answers[index].choice_text,
              correctAnswerChoice: answers[index].correct_answer,
              questionText: answers[index].question_text,
              answerExplanation: answers[index].explanation
            })
          }
          this.isLoaded = true
        })
        .catch(err => { console.log(err) })
    },
    safeIncrementIndex () {
      if (this.reviewIndex < this.answersForReview.length) {
        this.reviewIndex++
      }
    },
    safeDecreaseIncrementIndex () {
      if (this.reviewIndex > 0) {
        this.reviewIndex--
      }
    }
  }
}
</script>

