<script>
import axios from '@/axios-auth'
export default {
  data () {
    return {
      testData: null,
      totalQuestionsAttempted: null,
      totalQuestionsCorrect: null,
      examBreakdown: []
    }
  },
  mounted () {
    this.testData = this.$store.state.examSessionId
    console.log(this.testData)
    this.getExamBreakdown(this.$store.state.examSessionId.examSessionId)
  },
  methods: {
    getExamBreakdown: function (sessionId) {
      axios.post('api/exam/get-exam-breakdown/', {
        session_id: sessionId
      }, {
        headers: { Authorization: 'Token ' + this.$store.state.auth_token }
      })
        .then(res => {
          const data = res.data
          console.log(data)
          this.totalQuestionsAttempted = data.num_of_questions_total
          this.totalQuestionsCorrect = data.num_of_questions_correct
          if (data.num_of_medical_questions_total > 0) {
            this.examBreakdown.push({
              protocolGroup: 'General Adult and Pediatric Medical Protocols',
              numberOfQuestionsCorrect: data.num_of_medical_questions_correct,
              totalNumberOfQuestionsAttempted: data.num_of_medical_questions_total
            })
          }
          if (data.num_of_hazardous_materials_questions_total > 0) {
            this.examBreakdown.push({
              protocolGroup: 'Hazardous Materials Protocols',
              numberOfQuestionsCorrect: data.num_of_hazardous_materials_questions_correct,
              totalNumberOfQuestionsAttempted: data.num_of_hazardous_materials_questions_total
            })
          }
          if (data.num_of_trauma_questions_total > 0) {
            this.examBreakdown.push({
              protocolGroup: 'Trauma Protocols',
              numberOfQuestionsCorrect: data.num_of_trauma_questions_correct,
              totalNumberOfQuestionsAttempted: data.num_of_trauma_questions_total
            })
          }
          if (data.num_of_cardiac_arrest_questions_total > 0) {
            this.examBreakdown.push({
              protocolGroup: 'Critical / Cardiac Arrest Protocols',
              numberOfQuestionsCorrect: data.num_of_cardiac_arrest_questions_correct,
              totalNumberOfQuestionsAttempted: data.num_of_cardiac_arrest_questions_total
            })
          }
          if (data.num_of_GOP_questions_total > 0) {
            this.examBreakdown.push({
              protocolGroup: 'General Operating Procedures',
              numberOfQuestionsCorrect: data.num_of_GOP_questions_correct,
              totalNumberOfQuestionsAttempted: data.num_of_GOP_questions_total
            })
          }
          if (data.num_of_appendicies_questions_total > 0) {
            this.examBreakdown.push({
              protocolGroup: 'Appendices',
              numberOfQuestionsCorrect: data.num_of_appendicies_questions_correct,
              totalNumberOfQuestionsAttempted: data.num_of_appendicies_questions_total
            })
          }
        })
    }
  }
}
</script>

